import { Dialog, Stepper } from '@material-ui/core';
import { h } from 'preact';
import { useState } from 'preact/hooks';

import { api } from '../../utils/api';
import dispatchEvent from '../../utils/eventDispatcher';
import { PROVIDERS } from '../../utils/providers.enum';
import { LinkConfirmation } from '../link-confirmation';
import { LinkCredentials } from '../link-credentials';
import { LinkProvider } from '../link-provider';
import { LinkSplash } from '../link-splash';
import { LinkFooter } from './link-footer';
import { LinkHeader } from './link-header';

const shareProviderInfo = {
  id: '',
  saml_acs_url: '',
  saml_idp_url: '',
  saml_x509_certs: [],
};

const initialState = {
  [PROVIDERS.ADFSSAML]: {
    ...shareProviderInfo,
    saml_relying_party_trust_cert: '',
    saml_relying_party_trust_id: '',
  },
  [PROVIDERS.OktaSAML]: {
    ...shareProviderInfo,
    saml_entity_id: '',
  },
  [PROVIDERS.AzureSAML]: {
    ...shareProviderInfo,
    saml_entity_id: '',
  },
  [PROVIDERS.GoogleOAuth]: {
    domains: [''],
    id: '',
  },
};

interface CredentialsForm {
  id?: string;
  saml_acs_url?: string;
  saml_entity_id?: string;
  saml_idp_url?: string;
  saml_relying_party_trust_cert?: string;
  saml_relying_party_trust_id?: string;
  saml_x509_certs?: string[];
}

type Provider = PROVIDERS.ADFSSAML | PROVIDERS.OktaSAML | PROVIDERS.AzureSAML;

interface LinkProps {
  isOpen: boolean;
  onClose: (status: boolean) => void;
  name: string;
  projectId: string;
  domain: string;
  publishableKey: string;
}

export const Link = ({
  isOpen,
  onClose,
  name,
  projectId,
  domain,
  publishableKey,
}: LinkProps) => {
  const [activeStep, setActiveStep] = useState<number>(0);
  const [selectedProvider, selectProvider] = useState<string>('');
  const [credentialsData, onCredentialsFormChange] = useState<CredentialsForm>(
    {},
  );

  const onSelectProvider = (provider: string) => {
    selectProvider(provider);
    onCredentialsFormChange(initialState[provider as Provider]);
  };

  const onInputChange = (data: CredentialsForm) => {
    onCredentialsFormChange({ ...credentialsData, ...data });
  };

  const onNextStep = () => {
    setActiveStep((prevActiveStep: number) => prevActiveStep + 1);

    if (activeStep === 1) {
      return onDraftConnection();
    }

    if (activeStep === 2) {
      return onCredentialsFormSubmit();
    }
  };

  const onPreviouStep = () => {
    setActiveStep((prevActiveStep: number) => prevActiveStep - 1);
  };

  const onDraftConnection = async () => {
    const {
      data: { id, external_key },
    } = await api('/draft_connections/create', {
      method: 'POST',
      headers: { Authorization: `Bearer ${publishableKey}` },
      data: { name, project_id: projectId, connection_type: selectedProvider },
    });

    onCredentialsFormChange({
      ...credentialsData,
      ...{
        id,
        saml_acs_url: `https://auth.workos.com/sso/saml/acs/${external_key}`, // eslint-disable-line @typescript-eslint/camelcase
      },
    });
  };

  const onCredentialsFormSubmit = async () => {
    const { data } = await api('/draft_connections/update', {
      method: 'POST',
      headers: { Authorization: `Bearer ${publishableKey}` },
      data: {
        ...{ project_id: projectId, domains: [domain] },
        ...credentialsData,
      },
    });

    dispatchEvent('providerLinked', { ...data });
  };

  const getStepContent = (step: number) => {
    switch (step) {
      case 0:
        return <LinkSplash />;
      case 1:
        return (
          <LinkProvider
            selectedProvider={selectedProvider}
            selectProvider={onSelectProvider}
          />
        );
      case 2:
        return (
          <LinkCredentials
            selectedProvider={selectedProvider}
            credentialsData={credentialsData}
            onCredentialsFormChange={onInputChange}
          />
        );
      case 3:
        return <LinkConfirmation />;
    }
  };

  return (
    <Dialog
      disableBackdropClick
      disableEscapeKeyDown
      fullWidth
      maxWidth="md"
      open={isOpen}
      onClose={() => onClose(false)}
    >
      <LinkHeader
        title="Single Sign-On Configuration"
        onClose={() => onClose(false)}
        activeStep={activeStep}
        onPreviouStep={onPreviouStep}
      />

      <Stepper activeStep={activeStep}>{getStepContent(activeStep)}</Stepper>

      <LinkFooter
        text="Continue"
        onClick={onNextStep}
        activeStep={activeStep}
        disabled={activeStep === 1 && selectedProvider.length === 0}
      />
    </Dialog>
  );
};
