import { useState } from 'preact/hooks';

import parseXML from './xmlMetadataParser';

export function useDropFileHandler(initialState: any, callback: any) {
  const [value, setValue] = useState('');

  function setValueFromEvent(event: any) {
    setValue(event.target.value);
  }

  async function useDropFile(event: any) {
    const files = event?.target?.files;
    setValueFromEvent(event);

    if (files?.[0]?.type === 'text/xml') {
      const result = await parseXML(files[0]);

      callback({ ...initialState, ...result });
    } else {
      const reader = new FileReader();

      reader.onloadend = () => {
        if (reader.result) {
          const fileContent = reader.result.toString();
          callback({
            ...initialState,
            ...{ [event.target.name]: [fileContent] },
          });
        }
      };

      reader.readAsText(files[0]);
    }
  }

  return [value, useDropFile];
}
