import { Button, DialogActions } from '@material-ui/core';
import { h } from 'preact';

interface LinkFooterProps {
  activeStep: number;
  onClick: () => void;
  text: string;
  disabled: boolean;
}

export const LinkFooter = ({
  text,
  onClick,
  activeStep,
  disabled,
}: LinkFooterProps) => (
  <DialogActions>
    <p style={{ fontSize: '13px', color: '#A9A9A9' }}>Powered by WorkOS</p>

    {activeStep < 3 && (
      <Button
        style={{
          backgroundColor: '#fff',
          borderRadius: '2px',
          border: '1px solid #E4E4E4',
          fontSize: '13px',
          fontWeight: 500,
          boxShadow: '0 2px 4px rgba(0, 0, 0, .11)',
          textTransform: 'none',
          padding: '8px 40px',
        }}
        onClick={onClick}
        disabled={disabled}
      >
        {text}
      </Button>
    )}
  </DialogActions>
);
