import { DialogContent } from '@material-ui/core';
import { Fragment, h } from 'preact';
import { useState } from 'preact/hooks';

import { Input } from '../input';
import { InputFile } from '../input-file';
import { Text } from '../text';

import { useDropFileHandler } from '../../utils/use-drop-file-handler';

export const CredentialsAzure = ({
  credentialsData,
  onCredentialsFormChange,
}: any) => {
  const [isManualConfigEnabled, toggleManualConfig] = useState(false);
  const [fileValue, dropFile] = useDropFileHandler(
    credentialsData,
    onCredentialsFormChange,
  );

  const onInputChange = (event: any) => {
    const payload = { [event.target.name]: event.target.value };
    onCredentialsFormChange({ ...credentialsData, ...payload });
  };

  return (
    <DialogContent style={{ padding: '24px' }}>
      <Text>
        Use this ACS URL and Entity ID to configure your Azure settings.
      </Text>

      <Input
        copyable
        label="ACS URL"
        name="saml_acs_url"
        onChange={onInputChange}
        readOnly
        value={credentialsData.saml_acs_url}
      />

      <Input
        copyable
        label="Entity ID"
        name="saml_entity_id"
        onChange={onInputChange}
        readOnly
        value={credentialsData.saml_entity_id}
      />
      <Text>Then, add the details to configure your Connection.</Text>

      {isManualConfigEnabled ? (
        <Fragment>
          <Input
            label="Identity Provider SSO URL"
            name="saml_idp_url"
            onChange={onInputChange}
            placeholder="https://provider.sso"
            value={credentialsData.saml_idp_url}
          />

          <InputFile
            label="X.509 Certificate"
            name="saml_x509_certs"
            accept=".pem, .key, .crt, .cer, or .cert"
            onChange={dropFile}
            content={credentialsData.saml_x509_certs}
          />
        </Fragment>
      ) : (
        <InputFile
          label="Azure Metadata File"
          name="metadataFile"
          accept=".xml"
          onChange={dropFile}
          content={fileValue}
        />
      )}

      <p style={{ color: '#A9A9A9', fontSize: '13px', marginTop: '8px' }}>
        {isManualConfigEnabled
          ? 'Have an Azure metadata file?'
          : 'Don’t have a metadata file?'}{' '}
        <span
          style={{ textDecoration: 'underline', cursor: 'pointer' }}
          onClick={() => toggleManualConfig(!isManualConfigEnabled)}
        >
          {isManualConfigEnabled
            ? 'Configure automatically'
            : 'Configure manually'}
        </span>
        .
      </p>
    </DialogContent>
  );
};
