import { ArrowBackIos, Close } from '@material-ui/icons';
import { DialogTitle, IconButton, Typography } from '@material-ui/core';
import { h } from 'preact';

interface LinkHeaderProps {
  activeStep: number;
  onClose: () => void;
  onPreviouStep: () => void;
  title: string;
}

export const LinkHeader = ({
  activeStep,
  onClose,
  onPreviouStep,
  title,
}: LinkHeaderProps) => {
  return (
    <DialogTitle disableTypography>
      {activeStep > 0 && activeStep < 3 && (
        <IconButton
          onClick={onPreviouStep}
          style={{
            color: '#9094A6',
            position: 'absolute',
            left: '8px',
            top: '4px',
          }}
        >
          <ArrowBackIos />
        </IconButton>
      )}
      <Typography variant="h6">{title}</Typography>
      <IconButton
        onClick={onClose}
        style={{
          color: '#9094A6',
          position: 'absolute',
          right: '8px',
          top: '4px',
        }}
      >
        <Close />
      </IconButton>
    </DialogTitle>
  );
};
