import { h } from 'preact';
import { DialogContent } from '@material-ui/core';
import { AddOutlined, CloseOutlined } from '@material-ui/icons';
import { useState } from 'preact/hooks';

import { Input } from '../input';
import { Text } from '../text';

export const CredentialsGoogle = ({
  credentialsData,
  onCredentialsFormChange,
}: any) => {
  const [domainsList, onChangeDomainsList] = useState(['']);

  const onInputChange = (event: any) => {
    const [_name, index] = event.target.name.split('_');

    domainsList.splice(index, 1, event.target.value);
    onChangeDomainsList(domainsList);
    onCredentialsFormChange({ domains: domainsList });
  };

  const onAddDomain = () => {
    onCredentialsFormChange({
      ...credentialsData,
      ...{ domains: [...credentialsData.domains, ''] },
    });
  };

  const onRemoveDomain = () => {
    onCredentialsFormChange({
      ...credentialsData,
      ...{ domains: credentialsData.domains.slice(0, -1) },
    });
  };

  return (
    <DialogContent style={{ padding: '24px', textAlign: 'left' }}>
      <Text>
        Add any domains you’d like to support for your Google OAuth connection.
      </Text>

      <div
        style={{
          borderBottom: '1px solid #E4E4E4',
          marginBottom: '8px',
          paddingBottom: '4px',
        }}
      >
        <h3
          style={{
            fontSize: '13px',
            fontWeight: 500,
            marginBottom: '4px',
            color: '#666975',
          }}
        >
          Approved Domains
        </h3>
      </div>

      {credentialsData.domains.map((value: any, index: number) => {
        return (
          <div style={{ display: 'flex', marginBottom: '-16px' }}>
            <Input
              name={`domains_${index}`}
              onChange={onInputChange}
              placeholder="domain.com"
              value={value}
            />
            <CloseOutlined
              htmlColor="#D03D3D"
              onClick={onRemoveDomain}
              style={{
                marginTop: '12px',
                marginLeft: '8px',
                cursor: 'pointer',
                verticalAlign: 'top',
              }}
            />
          </div>
        );
      })}

      <span
        onClick={onAddDomain}
        style={{
          textAlign: 'left',
          cursor: 'pointer',
          fontSize: '13px',
          fontWeight: 600,
        }}
      >
        <AddOutlined style={{ marginRight: '4px', verticalAlign: 'middle' }} />
        Add Domain
      </span>
    </DialogContent>
  );
};
