import xmlParser from 'fast-xml-parser';

export default function parseXml(file: File): Promise<any> {
  const reader = new FileReader();
  return new Promise((resolve, reject) => {
    reader.onloadend = () => {
      if (reader.result) {
        const resultString = reader.result.toString();
        const valid = xmlParser.validate(resultString);

        if (!valid) {
          reject(new DOMException('Problem parsing XML file.'));
        }

        const parsed = xmlParser.parse(resultString, {
          attributeNamePrefix: '_',
          ignoreAttributes: false,
          ignoreNameSpace: true,
        });

        const extractedValues = {
          saml_entity_id: parsed.EntityDescriptor?._entityID,
          saml_idp_url: idpURL(parsed),
          saml_x509_certs: x509Certs(parsed),
        };

        resolve(extractedValues);
      }
      reject(new DOMException('Problem parsing XML file.'));
    };

    reader.readAsText(file);
  });
}

const descriptor = (parsed: any) =>
  parsed.EntityDescriptor?.IDPSSODescriptor ||
  parsed.EntityDescriptor?.SPSSODescriptor;

const idpURL = (parsed: any) =>
  descriptor(parsed)?.SingleSignOnService?.[0]?._Location;

const x509Certs = (parsed: any) => {
  let keys = descriptor(parsed)?.KeyDescriptor;
  if (!Array.isArray(keys)) {
    keys = [keys];
  }

  return keys.map((key: any) => {
    const rawValue = key.KeyInfo?.X509Data?.X509Certificate;

    if (!rawValue) {
      return null;
    }

    return [
      '-----BEGIN CERTIFICATE-----',
      rawValue,
      '-----END CERTIFICATE-----',
    ].join('\n');
  });
};
