import { Button, CssBaseline, ThemeProvider } from '@material-ui/core';
import { h } from 'preact';
import { useState } from 'preact/hooks';

import theme from '../theme';
import { Link } from './link-main';

interface AppProps {
  name: string;
  projectId: string;
  publishableKey: string;
  domain: string;
}

const App = ({ domain, name, projectId, publishableKey }: AppProps) => {
  const [isOpen, setOpen] = useState<boolean>(false);
  const onToggleLink = (status: boolean) => setOpen(status);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Button
        color="primary"
        onClick={() => onToggleLink(true)}
        variant="contained"
      >
        Configure SSO
      </Button>
      <Link
        isOpen={isOpen}
        onClose={() => onToggleLink(false)}
        name={name}
        projectId={projectId}
        publishableKey={publishableKey}
        domain={domain}
      />
    </ThemeProvider>
  );
};

export default App;
