import { DialogContent, Typography } from '@material-ui/core';
import { h } from 'preact';

import { PROVIDERS } from '../../utils/providers.enum';
import { Provider } from './provider';
import { ProviderADFS } from './provider-adfs';
import { ProviderAzure } from './provider-azure';
import { ProviderGoogle } from './provider-google';
import { ProviderOkta } from './provider-okta';

export const LinkProvider = ({ selectedProvider, selectProvider }: any) => (
  <DialogContent style={{ padding: '24px' }}>
    <Typography
      variant="h6"
      style={{
        fontSize: '13px',
        marginTop: 0,
        color: '#9094A6',
        textAlign: 'left',
        marginBottom: '16px',
      }}
    >
      Select your Identity Provider.
    </Typography>

    <ul
      style={{
        display: 'grid',
        justifyContent: 'space-between',
        gridTemplateColumns: 'repeat(2, 1fr)',
        gap: '24px',
        textAlign: 'center',
      }}
    >
      <Provider
        isSelected={selectedProvider === PROVIDERS.OktaSAML}
        onClick={() => selectProvider(PROVIDERS.OktaSAML)}
      >
        <ProviderOkta />
      </Provider>

      <Provider
        isSelected={selectedProvider === PROVIDERS.AzureSAML}
        onClick={() => selectProvider(PROVIDERS.AzureSAML)}
      >
        <ProviderAzure />
      </Provider>

      <Provider
        isSelected={selectedProvider === PROVIDERS.ADFSSAML}
        onClick={() => selectProvider(PROVIDERS.ADFSSAML)}
      >
        <ProviderADFS />
      </Provider>

      <Provider
        isSelected={selectedProvider === PROVIDERS.GoogleOAuth}
        onClick={() => selectProvider(PROVIDERS.GoogleOAuth)}
      >
        <ProviderGoogle />
      </Provider>
    </ul>
  </DialogContent>
);
