import { h, Fragment } from 'preact';
import { makeStyles } from '@material-ui/core';

const inputStyles = makeStyles({
  label: {
    marginBottom: '4px',
    fontWeight: 500,
    fontSize: '13px',
    color: '#666975',
    textAlign: 'left',
    display: 'block',
  },
  inputFile: {
    width: '100%',
    border: '1px solid #E0E1E4',
    borderRadius: '4px',
    position: 'relative',
    overflowY: 'auto',
    marginBottom: '24px',
  },
  inputFileContent: {
    maxWidth: '100%',
    textAlign: 'center',
    display: 'flex',
    alignItems: 'center',
    padding: '8px',
    minHeight: '125px',
    maxHeight: '150px',
  },
  input: {
    position: 'absolute',
    top: '0',
    left: '0',
    height: '100%',
    width: '100%',
    opacity: '0',
    cursor: 'pointer',
  },
  h3: {
    color: '#666975',
    fontSize: '13px',
    fontWeight: 500,
    marginBottom: '8px',
  },
  p: {
    color: '#9BA0B2',
    fontSize: '11px',
  },
  inputFilePlaceholder: {
    width: '75%',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
});

export const InputFile = ({ label, name, accept, content, onChange }: any) => {
  const classes = inputStyles();

  return (
    <Fragment>
      <label className={classes.label}>{label}</label>

      <div className={classes.inputFile}>
        <input
          type="file"
          name={name}
          accept={accept}
          onChange={onChange}
          className={classes.input}
        />

        <div className={classes.inputFileContent}>
          {content && content.length > 0 ? (
            <div className={classes.inputFilePlaceholder}>
              <h3 className={classes.h3}>File uploaded successfully</h3>
              <p className={classes.p}>{label} added.</p>
            </div>
          ) : (
            <div className={classes.inputFilePlaceholder}>
              <h3 className={classes.h3}>Drag and drop</h3>
              <p className={classes.p}>
                Drag or choose a file ({accept}) to upload.
              </p>
            </div>
          )}
        </div>
      </div>
    </Fragment>
  );
};
