import { h, Fragment } from 'preact';

import { CredentialsADFS } from './credentials-adfs';
import { CredentialsAzure } from './credentials-azure';
import { CredentialsGoogle } from './credentials-google';
import { CredentialsOkta } from './credentials-okta';

import { PROVIDERS } from '../../utils/providers.enum';

export const LinkCredentials = ({
  selectedProvider,
  credentialsData,
  onCredentialsFormChange,
}: any) => {
  const getProviderCredentials = (provider: string) => {
    switch (provider) {
      case PROVIDERS.ADFSSAML:
        return (
          <CredentialsADFS
            credentialsData={credentialsData}
            onCredentialsFormChange={onCredentialsFormChange}
          />
        );
      case PROVIDERS.AzureSAML:
        return (
          <CredentialsAzure
            credentialsData={credentialsData}
            onCredentialsFormChange={onCredentialsFormChange}
          />
        );
      case PROVIDERS.OktaSAML:
        return (
          <CredentialsOkta
            credentialsData={credentialsData}
            onCredentialsFormChange={onCredentialsFormChange}
          />
        );

      case PROVIDERS.GoogleOAuth:
        return (
          <CredentialsGoogle
            credentialsData={credentialsData}
            onCredentialsFormChange={onCredentialsFormChange}
          />
        );
    }
  };

  return <Fragment>{getProviderCredentials(selectedProvider)}</Fragment>;
};
