import { FormControl, InputBase, InputLabel } from '@material-ui/core';
import { CheckOutlined, FileCopyOutlined } from '@material-ui/icons';
import copy from 'copy-text-to-clipboard';
import { h } from 'preact';
import { useState } from 'preact/hooks';

export const Input = ({
  copyable,
  label,
  placeholder,
  readOnly,
  value,
  onChange,
  name,
}: any) => {
  const [isCopied, setIsCopied] = useState(false);

  const copyValue = () => {
    copy(value);
    setIsCopied(true);
    setTimeout(() => setIsCopied(false), 3000);
  };

  return (
    <FormControl>
      <InputLabel shrink>{label}</InputLabel>
      <InputBase
        name={name}
        value={value}
        placeholder={placeholder}
        readOnly={readOnly}
        onChange={onChange}
        endAdornment={
          copyable &&
          (isCopied ? (
            <CheckOutlined />
          ) : (
            <FileCopyOutlined onClick={copyValue} />
          ))
        }
      />
    </FormControl>
  );
};
